import { Component, OnInit } from "@angular/core";
import { CategoryService } from "../services/category.service";
import { Router, ActivatedRoute } from "@angular/router";

import swal from "sweetalert2";
import { Category } from "app/shared/models/category.model";

@Component({
  selector: "app-add-category",
  templateUrl: "./add-category.component.html",
  styleUrls: ["./add-category.component.scss"]
})
export class AddCategoryComponent implements OnInit {
  newcategory: Category;
  submitting: boolean;

  constructor(
    public categoryservice: CategoryService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.newcategory = new Category({});
  }

  ngOnInit() { }

  submit() {
    console.log("Submitted>>>>>>>>>", this.newcategory);

    swal
      .fire({
        title: "Do you want to add the category ?",
        text: "New category will be added in Category List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.categoryservice.addCategory(this.newcategory).subscribe(
            (data: any) => {
              swal.fire("Add!", "Category has been added.", "success").then(() => {
                this.router.navigate(['account/category']);
              })
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
