import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { CategoryService } from "./services/category.service";
import swal from "sweetalert2";
import { Category } from "../../../shared/models/category.model";

@Component({
  selector: "app-category",
  templateUrl: "./category.component.html",
  styleUrls: ["./category.component.scss"]
})
export class CategoryComponent implements OnInit {
  categoryList: Array<Category>;

  constructor(public categoryservice: CategoryService, private router: Router) {
    this.categoryList = [];
  }

  ngOnInit() {
    this.categoryservice.getCategory().subscribe(
      (data: any) => {
        console.log("categorylist>>", data);
        this.categoryList = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  edit(id) {
    console.log("category id>>", id);
    this.router.navigate(["account/manage/category/edit/" + id]);
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the category ?",
        text: "You won't be able to revert the deleted category",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.categoryservice.deleteCategory(id).subscribe(
            (data: any) => {
              swal.fire("Deleted!", "Category has been deleted.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
