import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { CategoryService } from "./services/category.service";
import swal from "sweetalert2";

@Component({
  selector: "app-category",
  templateUrl: "./category.component.html",
  styleUrls: ["./category.component.scss"]
})
export class CategoryComponent implements OnInit {
  categoryList;

  constructor(public categoryservice: CategoryService, private router: Router) {
    this.categoryList = [];
  }

  ngOnInit() {
    this.categoryservice.getCategory().subscribe(
      (data: any) => {
        this.categoryList = data;
        console.log(data);
      },
      error => {
        console.log(error);
      }
    );
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the Applicant?",
        text: "You won't be able to revert it back",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          this.categoryservice.deleteCategory(id).subscribe(
            (data: any) => {
              console.log("Deleted successfully");
              swal.fire("Deleted!", "Applicant Deleted.", "success");
              this.router.navigate(["/category"]);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Deleted!", "There was some error.", "error");
              console.log("Error", error);
            }
          );
        }
      });
  }
}
