import { Component, OnInit } from "@angular/core";

import { CategoryService } from "../services/category.service";
import { Router, ActivatedRoute } from "@angular/router";
import swal from "sweetalert2";
import { Category } from "app/shared/models/category.model";

@Component({
  selector: "app-edit-category",
  templateUrl: "./edit-category.component.html",
  styleUrls: ["./edit-category.component.scss"]
})
export class EditCategoryComponent implements OnInit {
  updatedcategory: Category;
  categoryid;

  constructor(
    public categoryservice: CategoryService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedcategory = new Category({});

    this.categoryid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.categoryservice.getCategoryById(this.categoryid).subscribe(
      (data: any) => {
        console.log("categorylist>>", data);
        this.updatedcategory = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  update() {
    console.log("Submitted>>>>>>>>>", this.updatedcategory);

    swal
      .fire({
        title: "Do you want to edit the category details ?",
        text: "You won't be able to revert the change",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Confirm"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.categoryservice.editCategory(this.updatedcategory).subscribe(
            (data: any) => {
              swal.fire("Edit!", "Category User has been edited.", "success").then(() => {
                this.router.navigate(['account/category']);
              })
              //dev
              console.log("Dataaa changeddd", data)
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
